<template>
  <div>
    <p>可拖拽排序</p>
    <s-table
      bordered
      draggable
      @onUpdate="onUpdate"
      :dragOption="dragOption"
      :columns="columns"
      :data-source="data"
      :customRow="customRow"
    >
      <template v-slot:action>
        <a href="javascript:;" class="edit">
          
        <a-icon type="menu-fold" class="edit"/>
        </a>
      </template>
    </s-table>
